"use client";
import { ChevronDown, Music, Mic2, CalendarDays, School, Group, CircleEqualIcon, Paperclip, PartyPopper, Gamepad } from 'lucide-react';
import { motion } from 'framer-motion';

export default function SearchFilter() {
    return (
        <section
            className="py-8 bg-gradient-to-br from-purple-50 to-blue-50"
            style={{ backgroundImage: 'linear-gradient(135deg, #f3e8ff 0%, #e0f2fe 100%)' }}
        >
            <div className="max-w-7xl mx-auto px-4 grid grid-cols-1 md:grid-cols-4 gap-4">
                {/* Date Selector */}
                <motion.div whileHover={{ scale: 1.02 }} className="relative">
                    <div className="flex items-center bg-white rounded-lg px-4 py-3 border border-gray-200">
                        <input type='date' placeholder='Select Event Month' className="w-full appearance-none bg-transparent focus:outline-none" />
                    </div>
                </motion.div>

                {/* University Selector */}
                <motion.div whileHover={{ scale: 1.02 }} className="relative">
                    <div className="flex items-center bg-white rounded-lg px-4 py-3 border border-gray-200">
                        <School className="w-5 h-5 text-blue-600 mr-2" />
                        <select className="w-full appearance-none bg-transparent focus:outline-none">
                            <option value="">Select University</option>
                            <option>University of Mountain</option>
                            <option>Tech Valley Institute</option>
                            <option>Metropolitan College</option>
                        </select>
                        <ChevronDown className="w-5 h-5 text-gray-400 ml-2" />
                    </div>
                </motion.div>

                {/* Event Type Selector */}
                <motion.div whileHover={{ scale: 1.02 }} className="relative">
                    <div className="flex items-center bg-white rounded-lg px-4 py-3 border border-gray-200">
                        <Mic2 className="w-5 h-5 text-pink-600 mr-2" />
                        <select className="w-full appearance-none bg-transparent focus:outline-none">
                            <option value="">Find Event Type</option>
                            <option className="flex items-center">Music Event</option>
                            <option className="flex items-center">Conference</option>
                            <option>Workshop</option>
                            <option>Exhibition</option>
                        </select>
                        <ChevronDown className="w-5 h-5 text-gray-400 ml-2" />
                    </div>
                </motion.div>

                {/* Search Button */}
                <motion.div
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                >
                    <button className="w-full h-full bg-gradient-to-r from-purple-600 to-blue-600 text-white rounded-lg px-6 py-3 font-semibold hover:shadow-lg transition-all flex items-center justify-center">
                        Search Events
                    </button>
                </motion.div>
            </div>

            {/* Event Categories */}
            <div className="max-w-7xl mx-auto px-8 mt-14 flex flex-wrap justify-between gap-4">
                <div className="flex items-center bg-white px-4 py-2 rounded-full border border-gray-200">
                    <Music className="w-5 h-5 text-purple-600 mr-2" />
                    <span>Music Events</span>
                </div>
                <div className="flex items-center bg-white px-4 py-2 rounded-full border border-gray-200">
                    <Mic2 className="w-5 h-5 text-blue-600 mr-2" />
                    <span>Conferences</span>
                </div>
                <div className="flex items-center bg-white px-4 py-2 rounded-full border border-gray-200">
                    <CalendarDays className="w-5 h-5 text-pink-600 mr-2" />
                    <span>Workshops</span>
                </div>
                <div className="flex items-center bg-white px-4 py-2 rounded-full border border-gray-200">
                    <PartyPopper className="w-5 h-5 text-pink-600 mr-2" />
                    <span>Annual Celebration</span>
                </div>
                <div className="flex items-center bg-white px-4 py-2 rounded-full border border-gray-200">
                    <Gamepad className="w-5 h-5 text-pink-600 mr-2" />
                    <span>Games</span>
                </div>
            </div>
        </section>
    );
}